import React, {useState} from "react";
import { Navbar, Informasi} from "./component";
import {
  Surat,
  Data,
  Footer,
  Home,
  Pemerintahan,
  Wilayah,
} from "./container";
import "./App.css";

const App = () => {
  return (
    <div className="App">
      <div className="gradient__bg">
        <Navbar />
        <Home />
      </div>
      <Pemerintahan />
      <Informasi />
      <Data />
      <Wilayah />
      <Surat />
      <div className="gradient__bg">
        <Footer />
      </div>
    </div>
  );
};

export default App;
